<template>
<div class="CacheBuild">
    <h1>{{ msg }}</h1>
    <div class="container">
        <div class="buttonContainer">
            <li>压缩文件\CacheBuild测试</li>
            <button @click="creatObject">创建DownloadTask对象</button>
            <button @click="setUrl">设置Url</button>
            <button @click="operateFunc">开始下载</button>
            <button @click="operateBuild">创建Build对象</button>
            <button @click="operateBuildFiles">设置BuildFiles</button>
            <button @click="operateBuildcacheUrls">设置BuildUrls</button>
            <button @click="operateBuildLabel">设置BuildLabel</button>
            <button @click="operateBuildstart">开始构建</button>

        </div>
        <div class="imgContainer" id="imgContainer">
            <pre class="progress" id="progress">{{ processTest }}</pre>
            <pre class="progressa" id="progress">{{ buildTest }}</pre>
        </div>
    </div>
</div>
</template>

<script>
import {
    yy_hybrid
} from '../YYConfig/YYJSbridge'

var myobject;
var doanloadFilePath;
var buildobject;

export default {
    name: 'CacheBuilder',
    props: {
        msg: String,
    },
    data: function () {
        return {
            processTest: "",
            buildTest: "",
        }
    },
    methods: {

        creatObject() {
            var _this = this;
            var callBack = function (_data) {
                var repsonse = _data.data;
                console.log(repsonse);
                if (repsonse.objectId) {
                    myobject = repsonse.objectId;
                    _this.processTest = "创建对象成功";
                } else {
                    _this.processTest = "创建对象失败";
                }
            }

            var parmas = {
                "name": "DownloadTask",
            }
            yy_hybrid("createObject", parmas, callBack);
        },
        setUrl() {
            var _this = this;
            var callBack = function (_data) {
                console.log("设置成功" + JSON.stringify(_data));
                _this.processTest = "设置链接成功";
            };
            var parmas = {
                "objectId": myobject,
                "props": {
                    "url": "http://172.23.19.204:8081/1.jpg"
                }
            };
            yy_hybrid("setProperties", parmas, callBack);
        },
        operateFunc() {
            var _this = this;
            var callBack = function (_data) {

                console.log("下载成功" + JSON.stringify(_data));
                var repsonse = _data.data;
                console.log(repsonse);
                var filePath = repsonse.file;
                doanloadFilePath = filePath;
                if (_data.errNo == 0) {
                    _this.processTest = "文件下载成功" + filePath;
                } else {
                    var errMsg = _data.errMsg;
                    console.log(errMsg);
                    _this.processTest = errMsg + filePath;
                }
            };
            var parmas = {
                "objectId": myobject,
                "operation": "start"
            };
            yy_hybrid("operate", parmas, callBack);
        },
        // build create
        operateBuild() {
            var _this = this;
            var callBack = function (_data) {
                var repsonse = _data.data;
                console.log(repsonse);
                if (repsonse.objectId) {
                    buildobject = repsonse.objectId;
                    _this.buildTest = "创建build对象成功";
                } else {
                    _this.buildTest = "创建对象失败";
                }
            }
            var parmas = {
                "name": "CacheBuilder",
            }
            yy_hybrid("createObject", parmas, callBack);
        },
        operateBuildFiles() {
            var _this = this;
            var callBack = function (_data) {
                console.log("设置files成功" + JSON.stringify(_data));
                _this.buildTest = "设置buildFiles成功" + doanloadFilePath;
            };

            var myfils = new Array()
            myfils[0] = doanloadFilePath;
            var parmas = {
                "objectId": buildobject,
                "props": {
                    "fromFiles": myfils
                }
            };
            yy_hybrid("setProperties", parmas, callBack);
        },
        operateBuildcacheUrls() {
            var _this = this;
            var callBack = function (_data) {
                console.log("设置cacheUrls成功" + JSON.stringify(_data));
                _this.buildTest = "设置buildcacheUrls成功";
            };

            var strs = new Array(); //定义一数组
            strs = doanloadFilePath.split("/"); //字符分割

            var fileName = strs[strs.length - 1];
            var cacheUrls = new Array()
            cacheUrls[0] = "zybhost://aa/" + fileName;
            var parmas = {
                "objectId": buildobject,
                "props": {
                    "cacheUrls": cacheUrls
                }
            };
            yy_hybrid("setProperties", parmas, callBack);
        },

        operateBuildLabel() {
            var _this = this;
            var callBack = function (_data) {
                console.log("设置Label成功" + JSON.stringify(_data));
                _this.buildTest = "设置Label成功";
            };
            var parmas = {
                "objectId": buildobject,
                "props": {
                    "label": "labelA"
                }
            };
            yy_hybrid("setProperties", parmas, callBack);
        },

        operateBuildstart() {
            var _this = this;
            var callBack = function (_data) {
                console.log("执行build start成功" + JSON.stringify(_data));
                _this.buildTest = "执行build start成功";
            };

            var parmas = {
                "objectId": buildobject,
                "operation": "start"
            };
            yy_hybrid("operate", parmas, callBack);
        }

    }
}
</script>

<style src ='./CacheBuild.css' scoped>
</style>
